import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {UserBookingsService} from '@pages/user-bookings/user-bookings.service';
import {tap} from 'rxjs/internal/operators';

@Component({
	selector: 'comment-dialog',
	templateUrl: './comment-dialog.component.html',
	styleUrls: ['./comment-dialog.component.scss']
})
export class CommentDialogComponent implements OnInit {
	formGroup: FormGroup;
	loading = true;
	comment: any;
	hasComment: boolean;

	constructor(
		@Inject(MAT_DIALOG_DATA) public data: any,
		private dialogRef: MatDialogRef<CommentDialogComponent>,
		private userBookingsService: UserBookingsService
	) {}

	ngOnInit(): void {
		this.formGroup = new FormGroup({
			commentControl: new FormControl('', [Validators.required, Validators.pattern('[\\s\\S]*\\S[\\s\\S]*')])
		});

		this.getComment(this.data);
	}

	getComment(reservationId: number): void {
		this.userBookingsService
			.getComment(reservationId)
			.pipe(tap(() => (this.loading = true)))
			.subscribe((data) => {
				this.comment = data;
				const errorApi = data === null || data.length === 0;
				this.hasComment = !errorApi;
				this.loading = false;
			});
	}

	close() {
		this.dialogRef.close();
	}

	submit() {
		this.dialogRef.close(this.formGroup.get('commentControl').value.trim());
	}
}
