import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	EventEmitter,
	Input,
	OnChanges,
	OnDestroy,
	OnInit,
	Output,
	ViewChild
} from '@angular/core';
import {AvailableFilters} from '@pages/booking-search/entities';
import {Observable, Subscription} from 'rxjs';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {BookingSearchFiltersService} from '@pages/booking-search/booking-search-filters.service';
import {map, startWith, take} from 'rxjs/operators';

import {MAT_MOMENT_DATE_FORMATS, MomentDateAdapter} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';
import * as _moment from 'moment';
import {default as _rollupMoment} from 'moment';
import {User} from '@entities/user';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {MatAutocompleteSelectedEvent} from '@angular/material/autocomplete';
import {MatChipInputEvent} from '@angular/material/chips';

const moment = _rollupMoment || _moment;

@Component({
	selector: 'filterboard',
	templateUrl: './filterboard.component.html',
	styleUrls: ['./filterboard.component.scss'],
	providers: [
		{provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
		{provide: MAT_DATE_FORMATS, useValue: MAT_MOMENT_DATE_FORMATS}
	],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class FilterboardComponent implements OnInit, OnDestroy, OnChanges {
	@Input() selectedFilters: AvailableFilters;
	@Input() hasData;
	@Input() cityId;
	@Input() userId;
	@Output() filtersChanged = new EventEmitter();
	@Output() showMapFlag = new EventEmitter();
	localShowMapFlag = false;
	buttonChangeView = 'Show map';
	isLoading = true;

	@Output() currentUserOutput = new EventEmitter();
	availableFilters: AvailableFilters = {
		seatTypes: [],
		repetitive: [],
		offices: [],
		dates: [],
		timeFinishes: [],
		timeStarts: [],
		floors: [],
		additionalFilters: [],
		weekDays: [
			{
				id: 0,
				name: 'Monday'
			},
			{
				id: 1,
				name: 'Tuesday'
			},
			{
				id: 2,
				name: 'Wednesday'
			},
			{
				id: 3,
				name: 'Thursday'
			},
			{
				id: 4,
				name: 'Friday'
			},
			{
				id: 5,
				name: 'Saturday'
			},
			{
				id: 6,
				name: 'Sunday'
			}
		]
	};

	filterSubscription: Subscription;
	officeSubscription: Subscription;
	seatTypeSubscription: Subscription;
	additionalFiltersSubscription: Subscription;
	floorsSubscription: Subscription;
	repetitiveSubscription: Subscription;
	usersSubscription: Subscription;

	today = moment().format('YYYY-MM-DD');
	aMonthFromNow = moment()
		.add(1, 'months')
		.format('YYYY-MM-DD');
	@Input() currentUser: User;

	filterForm = new FormGroup({
		officeId: new FormControl('', [Validators.required]),
		floorId: new FormControl(null),

		startDate: new FormControl(this.today, [Validators.required]),
		endDate: new FormControl(this.today, [Validators.required]),

		startTime: new FormControl('00:00', [Validators.required]),
		endTime: new FormControl('23:59', [Validators.required]),
		isMeeting: new FormControl(1, [Validators.required]),
		weekDay: new FormControl(),
		tagsId: new FormControl(),

		isRepetitive: new FormControl(1, [Validators.required]),

		currentUserId: new FormControl([]),

		usersId: new FormControl()
	});
	@Input() bookingUser: User;
	private bookingUserSubscription: Subscription;

	visible = true;
	removable = true;
	addOnBlur = false;
	separatorKeysCodes = [ENTER, COMMA];
	filteredUsers: Observable<User[]>;

	fruits = new Set();

	@ViewChild('userInput') userInput: ElementRef;

	users: User[];

	constructor(private bookingSearchFiltersService: BookingSearchFiltersService, public cd: ChangeDetectorRef) {
		this.filteredUsers = this.filterForm.get('usersId').valueChanges.pipe(
			startWith(null),
			map((user) => {
				if (!user) {
					return this.users.slice();
				}
				if (typeof user === 'string') {
					return this.filter(user);
				}
				if ('email' in user) {
					return this.filter(user.name);
				}
			})
		);
	}

	add(event: MatChipInputEvent): void {
		const input = event.input;
		const value = event.value;
		console.log(event);

		// Add our fruit
		if ((value || '').trim()) {
			this.fruits.add(value.trim());
		}

		// Reset the input value
		if (input) {
			input.value = '';
		}

		this.filterForm.get('usersId').setValue(null);
	}

	remove(fruit: any): void {
		const index = this.fruits.delete(fruit);

		/*if (index >= 0) {
			this.fruits.splice(index, 1);
		}*/
	}

	filter(userInput: string) {
		console.log(userInput);
		if (userInput) {
			return this.users.filter((user) => user.name.toLowerCase().indexOf(userInput.toLowerCase()) >= 0);
		}
		return this.users;
	}

	selected(event: MatAutocompleteSelectedEvent): void {
		// console.log(event);
		if (!('email' in event.option)) {
			this.fruits.add(event.option.value);
		}

		this.userInput.nativeElement.value = '';
		this.filterForm.get('usersId').setValue(null);
	}

	showMap() {
		this.localShowMapFlag = !this.localShowMapFlag;
		this.buttonChangeView = this.localShowMapFlag ? 'Show table' : 'Show map';
		this.showMapFlag.emit(this.localShowMapFlag);
	}

	getAvailableOffices(): void {
		this.officeSubscription = this.bookingSearchFiltersService
			.getAvailableOffices(this.cityId)
			.pipe(take(1))
			.subscribe((availableOffices) => (this.availableFilters.offices = availableOffices));
	}

	getUsers() {
		this.usersSubscription = this.bookingSearchFiltersService
			.getUsers()
			.pipe(take(1))
			.subscribe((users) => (this.users = users));
	}

	getCurrentSelectedOfficeId(): number {
		return this.filterForm.getRawValue().officeId;
	}

	getAvailableSeatTypes(): void {
		// Hardcoded userID
		this.seatTypeSubscription = this.bookingSearchFiltersService
			.getAvailableSeatTypes(1)
			.pipe(take(1))
			.subscribe((availableSeatTypes) => (this.availableFilters.seatTypes = availableSeatTypes));
	}

	getAvailableRepetitive(): void {
		// Hardcoded userID
		this.repetitiveSubscription = this.bookingSearchFiltersService
			.getAvailableRepetitive(1)
			.pipe(take(1))
			.subscribe((availableRepetitive) => (this.availableFilters.repetitive = availableRepetitive));
	}

	getAvailableAdditionalFilters(): void {
		this.additionalFiltersSubscription = this.bookingSearchFiltersService
			.getAvailableAdditionalFilters()
			.pipe(take(1))
			.subscribe(
				(availableAdditionalFilters) => (this.availableFilters.additionalFilters = availableAdditionalFilters)
			);
	}

	getAvailableFloors(officeID: number): void {
		this.isLoading = true;
		this.floorsSubscription = this.bookingSearchFiltersService
			.getAvailableFloors(officeID)
			.pipe(take(1))
			.subscribe((availableFloors) => {
				this.availableFilters.floors = availableFloors;
				this.isLoading = false;
			});
	}

	sendSelectedFilters(): void {
		this.localShowMapFlag = false;
		this.showMapFlag.emit(false);
		// Todo: Split into functions
		this.filterForm.patchValue({
			startDate: moment(this.filterForm.getRawValue().startDate).format('YYYY-MM-DD')
		});
		this.filterForm.patchValue({
			endDate: moment(this.filterForm.getRawValue().endDate).format('YYYY-MM-DD')
		});

		if (this.filterForm.getRawValue().isRepetitive === 1) {
			this.filterForm.patchValue({
				endDate: this.filterForm.getRawValue().startDate
			});
			this.filterForm.patchValue({
				weekDay: []
			});
		}

		if (this.filterForm.getRawValue().isMeeting === 2) {
			this.filterForm.patchValue({
				tagsId: []
			});
		}

		if (this.filterForm.getRawValue().endTime) {
			this.filterForm.patchValue({
				endTime: this.filterForm.getRawValue().endTime.concat(':59')
			});
		}
		if (this.filterForm.getRawValue().startTime) {
			this.filterForm.patchValue({
				startTime: this.filterForm.getRawValue().startTime.concat(':00')
			});
		}

		this.filterForm.patchValue({
			currentUserId: this.userId || this.currentUser.id
		});

		if (this.fruits) {
			const arr = Array.from(this.fruits);
			this.filterForm.patchValue({
				usersId: arr.map((user: User) => user.id)
			});
		}

		this.filtersChanged.emit(/*JSON.stringify(*/ this.filterForm.getRawValue()) /*)*/;
		this.cd.detectChanges();

		if (this.filterForm.getRawValue().endTime) {
			this.filterForm.patchValue({
				endTime: this.filterForm.getRawValue().endTime.slice(0, -3)
			});
		}
		if (this.filterForm.getRawValue().startTime) {
			this.filterForm.patchValue({
				startTime: this.filterForm.getRawValue().startTime.slice(0, -3)
			});
		}
		this.filterForm.markAsUntouched();
	}

	ngOnChanges() {}

	ngOnInit(): void {
		moment.locale('en-uk');
		this.getAvailableOffices();
		this.getAvailableSeatTypes();
		this.getAvailableAdditionalFilters();
		this.getAvailableRepetitive();
		this.getUsers();
	}
	unsubscribeCheck(sub: Subscription) {
		if (sub) {
			sub.unsubscribe();
		}
	}

	ngOnDestroy() {
		this.unsubscribeCheck(this.filterSubscription);
		this.unsubscribeCheck(this.floorsSubscription);
		this.unsubscribeCheck(this.officeSubscription);
		this.unsubscribeCheck(this.seatTypeSubscription);
		this.unsubscribeCheck(this.repetitiveSubscription);
		this.unsubscribeCheck(this.additionalFiltersSubscription);
		this.unsubscribeCheck(this.bookingUserSubscription);
		this.unsubscribeCheck(this.usersSubscription);
	}

	setTime() {
		this.filterForm.patchValue({
			startTime: '00:00'
		});
		this.filterForm.patchValue({
			endTime: '23:59'
		});
	}
}
