import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {AuthenticationComponent} from '@pages/authentication/authentication.component';
import {MainPageComponent} from '@pages/main-page/main-page.component';
import {OfficesComponent} from '@pages/offices/offices.component';
import {MapBuilderComponent} from '@shared/map-builder/map-builder.component';
import {DashboardComponent} from '@pages/booking-search/dashboard/dashboard.component';
import {UserManagementComponent} from '@pages/user-management/user-management.component';
import {StaffBookingsComponent} from '@pages/staff-bookings/staff-bookings.component';
import {UserBookingsComponent} from '@pages/user-bookings/user-bookings.component';
import {HotchairsGuard} from './hotchairs.guard';

const routes: Routes = [
	{path: 'login', component: AuthenticationComponent},
	{path: '', redirectTo: '/home', pathMatch: 'full'},
	{path: 'home', component: MainPageComponent},
	{
		path: 'offices',
		component: OfficesComponent,
		canActivate: [HotchairsGuard],
		data: {expectedRole: 'Admin,Office Manager'}
	},
	{path: 'booking-search/:cityId', component: DashboardComponent},
	{
		path: 'booking-search/:cityId/:userId',
		component: DashboardComponent,
		canActivate: [HotchairsGuard],
		data: {expectedRole: 'HR'}
	},
	{
		path: 'canvas/:floorId',
		component: MapBuilderComponent,
		canActivate: [HotchairsGuard],
		data: {expectedRole: 'Admin,Office Manager'}
	},
	{
		path: 'manage-roles',
		component: UserManagementComponent,
		canActivate: [HotchairsGuard],
		data: {expectedRole: 'Admin'}
	},
	{
		path: 'staff-bookings',
		component: StaffBookingsComponent,
		canActivate: [HotchairsGuard],
		data: {expectedRole: 'HR'}
	},
	{path: 'bookings', component: UserBookingsComponent},
	{path: '**', redirectTo: '/home', pathMatch: 'full'}
];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule]
})
export class AppRoutingModule {}
