import {
	AfterViewInit,
	Component,
	EventEmitter,
	Input,
	OnChanges,
	OnDestroy,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild
} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {DatePipe} from '@angular/common';
import {Reservation} from '@pages/user-bookings/entities';
import {MatDialog} from '@angular/material/dialog';
import {CommentDialogComponent} from '@pages/user-bookings/comment-dialog/comment-dialog.component';
import {Subscription} from 'rxjs';
import {DeleteDialogComponent} from '@pages/user-bookings/delete-dialog/delete-dialog.component';
import {NgxSpinnerService} from 'ngx-spinner';

@Component({
	selector: 'bookings-table',
	templateUrl: './bookings-table.component.html',
	styleUrls: ['./bookings-table.component.scss']
})
export class BookingsTableComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy {
	displayedColumns: string[] = ['place', 'time', 'date', 'floor', 'location', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	@Input() bookings: Reservation[];
	@Input() comment: any = [];
	@Output() updateTable = new EventEmitter<number>();
	@Output() addComment = new EventEmitter();
	dataSource: Reservation[] = [];
	loading = true;
	currentDate: string;
	subscription: Subscription = new Subscription();

	constructor(private datePipe: DatePipe, public dialog: MatDialog, private spiner: NgxSpinnerService) {
		this.currentDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
	}

	ngOnInit(): void {
		this.spiner.show();
	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.bookings && this.bookings) {
			this.dataSource = this.bookings;
			this.loading = false;
			this.spiner.hide();
			// To show how past reservations will look
			this.dataSource[this.dataSource.length - 1].date.start = '2020-08-01';
			this.dataSource[this.dataSource.length - 1].date.end = '2020-08-01';
		}
	}

	ngAfterViewInit() {}

	showCommentDialog(id: number) {
		this.subscription.add(
			this.dialog
				.open(CommentDialogComponent, {data: id})
				.afterClosed()
				.subscribe((comment) => {
					if (comment) {
						this.addComment.emit({reservationId: id, comment});
					}
				})
		);
	}

	showDeleteDialog(element: Reservation) {
		this.subscription.add(
			this.dialog
				.open(DeleteDialogComponent, {
					data: element
				})
				.afterClosed()
				.subscribe((result) => {
					if (result) {
						this.updateTable.emit(element.id);
					}
				})
		);
	}

	ngOnDestroy() {
		this.subscription.unsubscribe();
	}
}
